import { Component } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { PerfilPopover } from '../perfil/perfil.component';

export interface RouteInfo {
  path: string;
  title: string;
  iconType: string;
  icon: string;
}

export const ROUTES: RouteInfo[] = [
  {
    path: 'usuarios',
    title: 'Usuarios',
    iconType: 'custom',
    icon: 'assets/icons/users-solid.svg'
  },
  {
    path: 'fondos',
    title: 'Fondos',
    iconType: 'custom',
    icon: 'assets/icons/wallet-solid.svg'
  }
];

@Component({
  selector: 'app-administrador',
  templateUrl: 'administrador.page.html'
})

export class AdministradorPage {
  menus;
  title = '';
  tab = '';
  constructor(public popoverController: PopoverController) {
    this.menus = ROUTES;
  }

  changeTab(e) {
    this.tab = e.tab;
    this.title = e.tab.charAt(0).toUpperCase() + e.tab.slice(1);
  }

  async popoverPerfil(ev: any) {
    const popover = await this.popoverController.create({
      component: PerfilPopover,
      event: ev,
      translucent: true
    });
    return await popover.present();
  }

}
