import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdministradorPage } from './administrador.page';

const routes: Routes = [
  {
    path: 'administrador',
    component: AdministradorPage,
    children: [
      {
        path: 'usuarios',
        loadChildren: () =>
          import('./usuarios/usuarios.module').then(m => m.UsuariosPageModule)
      },
      {
        path: 'fondos',
        loadChildren: () =>
          import('./fondos/fondos.module').then(m => m.FondosPageModule)
      }
    ]
  },
  {
    path: '',
    redirectTo: 'administrador/usuarios',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdministradorPageRoutingModule {}
